.box{
    
    margin-top: 10px;
    border:2px solid gray;
	width: 360px;
    display: flex;
    
	height: 50px;

	background-color:#F2F2F2;
    

	border-radius: 30px;

	display: flex;

	align-items: center;

	padding: 20px;

}

.box > btn{
    border:none;

}

.box > input{
    
    background-color:#F2F2F2;

	flex: 1;

    width: 360px;

	height: 40px;

	border: none;

	outline: none;

	font-size: 15px;

	


}
.navbar{
    
    width: 100%;
}

.btn{
    border: none;
    color:blue;
}
.row{
    display: flex;
    margin-left: 1px;
}
.bottom-left h1{
    color:white;
    margin-left: 40px;
    margin-top:250px;
}
.bottom-left p{
    color:white;
    margin-left: 45px;
    
}